$badge-size: 30px;
$margin-item-left: 50px;

.timeline {
  border-left: 2px solid lightgray;
}

.timeline-label {
  position: relative;
  left: 35px;
  top: 15px;
  width: 80%;
  height: 60px;

  font-weight: bold;
  margin: 0;
}

.timeline-badge {
  width: $badge-size;
  height: $badge-size;

  position: relative;
  top: 20px;
  left: -($badge-size/2);

  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 50%;

  &-seminar {
    @extend .timeline-badge;
    background-color: #F7EF99;
  }

  &-international {
    @extend .timeline-badge;
    background-color: #E0A458;
  }

  &-personal {
    @extend .timeline-badge;
    background-color: #E637BF;
  }

  &-engagement {
    @extend .timeline-badge;
    background-color: #419D78;
  }

  &-innovation {
    @extend .timeline-badge;
    background-color: #DDE0BD;
  }
}

.timeline-item {
  height: auto;
}

.timeline-panel {
  position: relative;
  top: -20px;
  left: 35px;
  width: 85%;

  border-radius: 8px;

  // Shadow
  -webkit-box-shadow: 0 0 10px -1px rgba(211, 211, 211, 1);
  -moz-box-shadow: 0 0 10px -1px rgba(211, 211, 211, 1);
  box-shadow: 0 0 10px -1px rgba(211, 211, 211, 1);

  // Transition
  transition: 0.3s;

  .card-img {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }

  .badge-location {
    color: white;
    background-color: #4E598C;
  }
}

@media (min-width: 992px) {
  .container {
    width: 40%;
  }

  .timeline-panel:hover {
    transform: scale(1.02);
  }
}

